.creators-section {
  padding: 4.8rem 0;
}

.creators-heading-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 8rem;
}

.creators-grid {
  height: 40rem;
}

.creator-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 2.4rem 4.8rem rgba(0, 0, 0, 0.075);
  border-radius: 11px;
  overflow: hidden;
  transition: all 0.4s;
  background: linear-gradient(113.49deg, #c73d54 -30.3%, #16256b 58.12%);
  position: relative;
}

.creator-card:hover {
  transform: translateY(-1.2rem);
  box-shadow: 0 0 6.4rem goldenrod;
}

.creator-banner {
  width: 100%;
  height: 40%;
  object-fit: cover;
}

.creator-img {
  width: 25%;
  border: 2px solid #e3590f;
  border-radius: 10px;
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
  position: absolute;
  left: 50%;
  top: 40%;
  transform: translate(-50%, -50%);
}

.creator-info {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  justify-items: center;
  padding: 2.4rem;
  margin-top: 2.4rem;
}

.creator-name {
  grid-column: 1 / -1;
  font-size: 2.4rem;
  color: #ded6d6;
  font-weight: 600;
}

.creator-stat {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.2rem;
  font-size: 1.6rem;
  color: #ded6d6;
}

.creator-stat p:nth-child(2) {
  font-size: 2rem;
  font-weight: 500;
  color: goldenrod;
}

.creator-button {
  background-color: #ff4500ff;
  font-size: 1.6rem;
  padding: 0.8rem 1.2rem;
  width: 75%;
}

.creator-button:hover,
.creator-button:active {
  background-color: #dc4b05;
}
